  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item>厂家往来</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">厂家付款单管理</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看付款单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="财务组织:" size="mini">
          {{params.company_name}}
        </el-form-item>
        <el-form-item label="单据号:" size="mini">
          {{params.payment_sn}}
        </el-form-item>
        <el-form-item label="供应商(厂家)" v-if="params.factory_accounts_type_id!=20002" size="mini">
          {{params.supplier_name}}
        </el-form-item>
        <el-form-item label="应付余额" size="mini">
          <span>{{params.balance||0.00}}</span>
        </el-form-item>
        <el-form-item label="联系人" size="mini">
          <span>{{params.principal_name||''}}</span>
        </el-form-item>
        <el-form-item label="联系电话" size="mini">
          <span>{{params.principal_mobile||''}}</span>
        </el-form-item>
        <el-divider></el-divider>
        <el-form-item label="业务日期:" size="mini">
          {{params.business_time}}
        </el-form-item>
        <el-form-item label="往来类型:" size="mini">
          {{params.factory_accounts_type_name}}
        </el-form-item>
        <el-form-item :label="params.factory_accounts_type_name == '运费付款' ? '物流公司:' : '供应商(厂家):'" size="mini">
          <template v-if="params.factory_accounts_type_name == '运费付款'">
            {{params.logistics_name}}
          </template>
          <template v-else>
            {{params.supplier_name}}
          </template>
        </el-form-item>
        <el-form-item label="部门:" size="mini">
          {{params.department_name}}
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          {{params.employee_name}}
        </el-form-item>
        <el-form-item label="备注:" size="mini">
          {{params.remark}}
        </el-form-item>
        <el-form-item label="审核状态:" size="mini">
          {{params.auditor_status_name}}
        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i> <span>结算明细</span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="addTableData" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="序号">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_name" label="结算方式">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="开户银行">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_number" label="银行账号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>
    </div>
    <div class="aboutList" v-if="is_disabled">
      <div class="paymanageTitle fcsb">
        <span>核销明细</span>
      </div>
      <div class="table_container">
        <div>
          <el-table :highlight-current-row="true" tooltip-effect="light" border :data="instorageTable" show-summary :summary-method="getSummariesAboutlist" max-height="600">
            <el-table-column :show-overflow-tooltip="true" label="序号">
              <template slot-scope="scope">
                {{scope.$index+1}}
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_sn" label="单据号"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="type_name" label="单据类型"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="供应商(厂家)"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="instorage_num" label="采购数量" align="right"></el-table-column>
            <el-table-column v-if="params.factory_accounts_type_id==20001" :show-overflow-tooltip="true" property="order_price" label="采购金额" align="right"></el-table-column>
            <el-table-column :visible="Boolean(is_company_service)" :show-overflow-tooltip="true" property="rebate_price" label="使用返利" align="right"></el-table-column>
            <el-table-column :visible="Boolean(is_company_service)" :show-overflow-tooltip="true" property="payable_price" label="实际应付金额" align="right"></el-table-column>
            <el-table-column v-if="params.factory_accounts_type_id==20002" :show-overflow-tooltip="true" property="order_price" label="其它金额" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="200"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" width="200"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="price" label="已付款金额" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rest_price" label="未付款金额" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_price" label="本次付款金额" align="right"></el-table-column>
          </el-table>
        </div>

      </div>
      <p style="margin-top: 10px;font-size: 13px;">
        注：入库金额：不包含其他费用
      </p>
    </div>
    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="params.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="params.auditor_status==1">审 核</el-button>
        <el-button @click="abolishFun">作 废</el-button>
      </template>
      <!-- 已审核 -->
      <template v-if="params.auditor_status==2">
        <el-button plain @click="AUDIT_GET" v-if="authority.b_nopass==1">弃审</el-button>
      </template>
      <template v-if="params.auditor_status==2 || params.auditor_status==3">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <template v-else>
        <el-button @click="goBack">取 消</el-button>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchasePaymanagesearch",
  data() {
    return {
      authority: {},
      addTableData: [],
      params: {},
      is_disabled: false,
      instorageTable: [],
    };
  },
  computed: {
    // ...mapGetters(["paymanageSearch_showGet"]),
  },
  methods: {
    // ...mapActions(["paymanageIndexACT"]),
    goBack() {
      // this.paymanageIndexACT({
      //   //commont: "Index",
      //   params: this.paymanageSearch_showGet.params,
      //   goPage: this.paymanageSearch_showGet.goPage,
      //   pageSize: this.paymanageSearch_showGet.pageSize,
      // });
      this.$router.push({ path: "/purchase/paymanage" });
    },
    getSupplier(val) {
      this.$axMethod
        .getSupplierInfo({
          company_id: this.params.company_id,
          supplier_id: val,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            [
              "associated_supplier_id",
              "balance",
              "principal_mobile",
              "principal_name",
            ].forEach((item) => {
              this.$set(this.params, item, data.data[item]);
            });
          }
        });
    },
    examineFun(status) {
      //审核
      let payment = this.$route.query.payment_id;
      this.$axMethod
        .purchaseAuditorPass({
          payment_id: payment,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: "审核成功",
              type: "success",
            });
            this.selmethons();
          }
        });
    },
    abolishFun() {
      //作废
      let payment = this.$route.query.payment_id;
      this.$axMethod
        .purchaseAuditorInvalid({
          payment_id: payment,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: "作废成功",
              type: "success",
            });
            this.selmethons();
            this.goBack();
          }
        });
    },
    AUDIT_GET() {
      this.$axMethod
        .auditorNoPassF({
          payment_id: this.$route.query.payment_id,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success",
            });
            this.goBack();
          }
        });
    },
    getSummariesAboutlist(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]);
          } else if ([8, 9, 10].includes(index)) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    selmethons() {
      let obj = {
        payment_id: this.$route.query.payment_id,
      };
      this.$axMethod.purchaseGetPaymentInfo(obj).then(({ data }) => {
        if (data.code == 200) {
          let datataple = data.data;
          this.$set(this, "params", datataple.paymentInfo);
          this.getSupplier(datataple.paymentInfo.supplier_id);
          this.$set(this, "addTableData", datataple.paymentCommonList);
          this.$set(this, "instorageTable", datataple.list);
          if (datataple.list.length) {
            this.$set(this, "is_disabled", true);
          }
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.selmethons();
  },
};
</script>

